@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,500;0,700;1,400&display=swap);
* {
  box-sizing: border-box;
}
body {
  font-family: 'Roboto', sans-serif;
  
}

html,
body,
ul,
li,
button,
input,
textarea,
h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  margin: 0;
  padding: 0;
  border-width: 0;
}

.app {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  width: 1440px;
  height: 100vh;
  background-color: #FBFCFF;
}

.container {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-direction: column;
          flex-direction: column;
  box-shadow: 0px 8px 28px rgba(0, 0, 0, 0.08);
  border-radius: 8px;
}

.card-container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  
}


.card {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  margin-left: 32px;
  margin-right: 32px;
}
.card:last-child {
  margin-left: 0;
}

.card__heading {
  font-weight: 700;
  font-size: 48px;
  margin: 28px 0 48px;
}


.form-groop {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  margin-bottom: 36px;
}
.card__select {
  width: 313px;
  height: 46px;
  border-radius: 4px;
  border: 1px solid #CBCBCB;
  padding-left: 20px;
  padding-right: 20px;
  font-weight: 500;
  font-size: 18px;
}
.card__input {
  width: 313px;
  height: 46px;
  margin-top: 20px;
  padding-left: 20px;
  padding-right: 20px;
  border-radius: 8px;
  border: 1px solid #CBCBCB;
}

.button-container {
  margin-bottom: 28px;
  
}
.button {
  background-color: #58B4AE;
  border-radius: 4px;
  text-align: center;
  font-weight: 700;
  font-size: 14px;
  padding: 16px 32px;
  color: #FBFCFF;
}
.button:hover {
  background-color: #9BD2CE;


}
